import React from "react"
import Pagination from "react-bootstrap/Pagination"
import styled, { css } from "styled-components"

const StyledWrapper = styled.div`
  justify-content: center;
  display: flex;
  margin-top: 15px;
`

const StyledPaginationItem = styled(Pagination.Item)`
  > .page-link {
    cursor: pointer;
    background-color: transparent;
    border: none;
    text-align: center;
    display: block;
    border-radius: 5px;
    &:hover {
      background-color: rgb(255, 255, 255, 0.5);
    }
    /* &.active {
      background-color: green !important;
    } */
    ${({ active }) =>
      active &&
      css`
         {
          background-color: #84c225 !important;
        }
      `}
  }
  > a {
    color: black !important;
  }
`

const generateItems = (pageChanged, active, numberOfPages) => {
  const items = []
  let firstPage = active - 2
  if (firstPage < 1) firstPage = 1
  let lastPage = active + 2
  if (lastPage > numberOfPages) lastPage = numberOfPages
  for (let number = firstPage; number < lastPage + 1; number += 1) {
    items.push(
      <StyledPaginationItem
        onClick={pageChanged}
        key={number}
        active={number === active}
      >
        {number}
      </StyledPaginationItem>
    )
  }
  return items
}

const paginationBasic = ({ onClick, active, numberOfPages }) => (
  <StyledWrapper>
    {/* <Pagination size="lg">
      {generateItems(onClick, active, numberOfPages)}
    </Pagination> */}
  </StyledWrapper>
)

export default paginationBasic
