import React, { useState } from "react"
import styled from "styled-components"
import ModalImage from "react-modal-image"
import Pagination from "../components/organisms/Pagination"

const StyledGrid = styled.div`
  display: grid;
  align-items: center;
  grid-gap: 10px;
  grid-template-columns: repeat(4, 1fr);

  @media (max-width: 900px) {
    grid-template-columns: repeat(3, 1fr);
  }

  @media (max-width: 680px) {
    grid-template-columns: repeat(2, 1fr);
  }

  @media (max-width: 490px) {
    grid-template-columns: repeat(1, 1fr);
  }
  > div {
    margin: auto;
  }
`

const Header = styled.div`
  color: white;
  font-size: 60px;
  font-family: "Poiret One", cursive;
  font-weight: 500;
  text-align: center;
  cursor: default;
  text-shadow: 2px 2px 2px black;
  margin-bottom: 10px;
`
const StyledModalImage = styled(ModalImage)`
  width: 180px;
  height: 200px;
  background-color: rgb(66, 69, 66, 0.4);
  margin: 5px;
  border: 2px solid white;
  border-radius: 5px;
`

const EmptyView = styled.h2`
  margin: auto;
  color: white;
  cursor: default;
  font-family: "Poiret One", cursive;
  font-size: 50px;
  font-weight: 600;
`

const GalleryTemplate = ({ photos, pageName }) => {
  const [pageNumber, setPageNumber] = useState(1)
  return photos && photos.allFile.edges.length > 0 ? (
    <div style={{ margin: "auto" }}>
      <Header>{pageName}</Header>
      <StyledGrid>
        {photos.allFile.edges
          // .slice(12 * (pageNumber - 1), 12 * pageNumber)
          .map((photo, index) => (
            <StyledModalImage
              key={`jpg-${index}`}
              hideDownload="true"
              hideZoom="true"
              small={photo.node.publicURL}
              large={photo.node.publicURL}
            />
          ))}
      </StyledGrid>
      {/* <Pagination
        onClick={e => {
          setPageNumber(parseInt(e.target.text, 10))
        }}
        active={pageNumber}
        numberOfPages={photos.length / 12}
      /> */}
    </div>
  ) : (
    <EmptyView>Nothing here</EmptyView>
  )
}

export default GalleryTemplate

